.selected, .selectedicon{
    color: #06A66A;
    padding: 10px 0;
    position: relative;
    letter-spacing: 2px;
}

.lilink, .lilinkicon{
    padding: 10px 0;
    position: relative;
    letter-spacing: 2px;
}

.lilink::before,
.lilink::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: rgba(13, 200, 35, .4);
    left: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transition: all .3s;
    transition: all .3s;
}


.lilink::before{
    top: 0;
    -webkit-transform-origin: left;
            transform-origin: left;
}
.lilink::after{
    bottom: 0;
    -webkit-transform-origin: right;
            transform-origin: right;
}

.lilink:hover::before,
.lilink:hover::after{
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}




.selected::before,
.selected::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: rgba(13, 200, 35, .4);
    left: 0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transition: all .3s;
    transition: all .3s;
}


.selected::before{
    top: 0;
    -webkit-transform-origin: left;
            transform-origin: left;
}
.selected::after{
    bottom: 0;
    -webkit-transform-origin: right;
            transform-origin: right;
}

.selected:hover::before,
.selected:hover::after{
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

.custbadge{
